@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap');

*{
  margin: 0;
  font-family: 'Outfit', sans-serif;
}

body{
  width: 100vw;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background:  -webkit-gradient(linear, left top, right top, from(#ece9e6), to(#ffffff));
  background:  -o-linear-gradient(left, #ece9e6, #ffffff);
  background:  linear-gradient(to right, #ece9e6, #ffffff);
}

.container{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 20vw;
  height: 45vh;
  padding: 30px;
  border: none;
  border-radius: 25px;
  -o-object-fit: contain;
     object-fit: contain;
  background-repeat: no-repeat;
  -webkit-box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
          box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.container form{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  padding-bottom: 30px;
}

.text_field{
  width: 60%;
  height: 30px;
  text-align: center;
  font-weight: 500;
  text-transform: capitalize;
  border: 1px solid transparent;
  border-radius: 25px 0px 0px 25px;
  outline: none;
  -webkit-box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
          box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
  cursor: pointer;
  -webkit-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}

.text_field:hover{
  background-color: #131d2d;
  color: white;
}

.submit_b{
  height: 100%;
  background-color: #131d2d;
  border: 1px solid transparent;
  border-radius: 0 25px 25px 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  -webkit-box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
          box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
  font-weight: 500;
  color: white;
  -webkit-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

.submit_b:hover{
  background-color:white ;
  color: #131d2d;
}

.wrapper{
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  width: 100%;
  height: 100%;
}

.wrapper span{
  color: #131d2d;
  font-weight: 600;
}

.temp{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 3rem;
}

.temp_max_min{
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  font-weight: 500;
}

.humidity{
  text-align: center;
  font-weight: 500;
}

.status{
  text-transform: capitalize;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  text-align: center;
  font-weight: 500;
}

.search_container{
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-weight: 700;
  font-size: 1.3rem;
}

.search_container svg{
  -webkit-animation: floating 3s infinite ease-in-out;
          animation: floating 3s infinite ease-in-out;
}


@-webkit-keyframes floating {
  from { -webkit-transform: translate(0,  0px); transform: translate(0,  0px); }
  65%  { -webkit-transform: translate(15px, 0); transform: translate(15px, 0); }
  to   { -webkit-transform: translate(0, -0px); transform: translate(0, -0px); }    
}


@keyframes floating {
  from { -webkit-transform: translate(0,  0px); transform: translate(0,  0px); }
  65%  { -webkit-transform: translate(15px, 0); transform: translate(15px, 0); }
  to   { -webkit-transform: translate(0, -0px); transform: translate(0, -0px); }    
}


@media only screen and (max-width: 950px) {

    .container{
      width: 30vw;
    }

}

@media only screen and (max-width: 650px) {
  .container{
    width: 35vw;
  }
}

@media only screen and (max-width: 550px) {
  .container{
    width: 40vw;
  }
}

@media only screen and (max-width: 450px) {
  .container{
    width: 50vw;
    height: 45vh;
  }
}

@media only screen and (max-width: 450px) {
  .container{
    width: 55vw;
    height: 45vh;
  }
}

@media only screen and (min-height: 1000px) {
  .container{
    height: 30vh;
  }
}

@media only screen and (max-height: 650px) {
  .container{
    height: 50vh;
  }
}